import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  getAllSpecialty,
  getSubscritionTypes,
  getAllNationality,
} from "api/employee";

export const getSubscriptionTypes = createAsyncThunk(
  "empolyee/getSubscriptionTypes",
  getSubscritionTypes
);
export const getSpecialties = createAsyncThunk(
  "empolyee/getAllSpecialties",
  getAllSpecialty
);

export const getNationality = createAsyncThunk(
  "empolyee/getNationality",
  getAllNationality
);

const empolyee = createSlice({
  name: "empolyee",
  initialState: {
    specialties: [],
    subscriptionTypes: [],
    nationalties: [],
    loading: false,
    error: null,
  },
  reducers: {
    addSpecialties: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: null,
        specialties: payload,
      };
    },
    addSubsriptionTypes: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: null,
        subscriptionTypes: payload,
      };
    },
  },
  extraReducers: {
    // subscriptionTypes
    [getSubscriptionTypes.pending]: (state) => {
      state.loading = true;
    },
    [getSubscriptionTypes.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.subscriptionTypes = payload;
      state.error = null;
    },
    [getSubscriptionTypes.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || "error";
      state.subscriptionTypes = {};
    },
    // all specialties
    [getSpecialties.pending]: (state) => {
      state.loading = true;
    },
    [getSpecialties.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.specialties = payload;
      state.error = null;
    },
    [getSpecialties.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || "error";
      state.specialties = {};
    },
    // all Nationalities
    [getNationality.pending]: (state) => {
      state.loading = true;
    },
    [getNationality.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.nationalties = payload;
      state.error = null;
    },
    [getNationality.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || "error";
      state.nationalties = {};
    },
  },
});

export const { addSubsriptionTypes, addSpecialties } = empolyee.actions;
export default empolyee.reducer;
