import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  signInWithPopup,
  signOut,
} from "firebase/auth";
import { authentication } from "firebaseConfig/firebaseConfig";
import server from "./server";

export const registerUserApi = async ({
  email,
  password,
  date_of_birth,
  first_name,
  last_name,
}) => {
  // firebase registration
  const firebaseUser = await createUserWithEmailAndPassword(
    authentication,
    email,
    password
  );
  const { _tokenResponse } = firebaseUser || {};
  const { idToken } = _tokenResponse || {};

  const bodyObject = {
    idToken,
    last_name,
    first_name,
    date_of_birth,
    email,
    notification: false,
  };
  const response = await server.post("auth/register", bodyObject);
  const { _id: userId } = response?.data?.data;
  localStorage.setItem("userId", userId);

  return response?.data?.data;
};

export const signInUserApi = async ({ email, password }) => {
  const firebaseUser = await signInWithEmailAndPassword(
    authentication,
    email,
    password
  );
  const { _tokenResponse: tokenResponse } = firebaseUser || {};
  const { idToken } = tokenResponse || {};
  const response = await server.post("auth/login", { idToken });
  const { _id: userId, token: userToken } = response?.data?.data;
  localStorage.setItem("userId", userId);
  localStorage.setItem("userToken", userToken);
  return response?.data?.data;
};

export const signInSocialUserApi = async ({ provider }) => {
  const firebaseUser = await signInWithPopup(authentication, provider);

  const { _tokenResponse: tokenResponse } = firebaseUser || {};
  const { idToken } = tokenResponse || {};

  const response = await server.post("auth/social", { idToken });
  console.log(response?.data?.data, "response?.data?.data");
  const { _id: userId } = response?.data?.data;
  localStorage.setItem("userId", userId);
  return response?.data?.data;
};

export const signOutUserApi = async () => {
  await signOut(authentication);
  localStorage.removeItem("userId");
  localStorage.removeItem("userToken");
};
