import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  registerUserApi,
  signInSocialUserApi,
  signInUserApi,
  signOutUserApi,
} from "api/authentication";
import { getUsersCv } from "api/employee";
import { editUserProfileDataApi, getUserProfileDataApi } from "api/profile";

export const registerUser = createAsyncThunk(
  "authentication/registerUser",
  registerUserApi
);
export const usersCv = createAsyncThunk(
  "authentication/getUsersCv",
  getUsersCv
);
export const signInUser = createAsyncThunk(
  "authentication/signInUser",
  signInUserApi
);

export const signInSocialUser = createAsyncThunk(
  "authentication/signInSocialUser",
  signInSocialUserApi
);
export const signOutUser = createAsyncThunk(
  "authentication/signOutUser",
  signOutUserApi
);

export const getUserProfileData = createAsyncThunk(
  "authentication/getUserProfileData",
  getUserProfileDataApi
);
export const editUserProfileData = createAsyncThunk(
  "authentication/editUserProfileData",
  editUserProfileDataApi
);

const authSlice = createSlice({
  name: "authentication",
  initialState: {
    isLoggedIn: false,
    user: {},
    cv: null,
    loading: false,
    error: null,
  },
  reducers: {
    logInUser: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: null,
        user: payload,
        isLoggedIn: true,
      };
    },
  },
  extraReducers: {
    // register
    [registerUser.pending]: (state) => {
      state.loading = true;
    },
    [registerUser.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.user = payload;
      state.isLoggedIn = true;
      state.error = null;
    },
    [registerUser.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || "error";
      state.user = {};
    },

    // register social user
    [signInSocialUser.pending]: (state) => {
      state.loading = true;
    },
    [signInSocialUser.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.user = payload;
      state.isLoggedIn = true;
      state.error = null;
    },
    [signInSocialUser.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || "error";
      state.user = {};
    },

    // sign in
    [signInUser.pending]: (state) => {
      state.loading = true;
    },
    [signInUser.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.user = payload;
      state.isLoggedIn = true;
      state.error = null;
    },
    [signInUser.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || error || "error";
      state.user = {};
    },

    // sign out
    [signOutUser.pending]: (state) => {
      state.loading = true;
    },
    [signOutUser.fulfilled]: (state) => {
      state.loading = false;
      state.user = {};
      state.isLoggedIn = false;
      state.error = null;
    },
    [signOutUser.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || "error";
      state.user = {};
    },

    // get user profile
    [getUserProfileData.pending]: (state) => {
      state.loading = true;
    },
    [getUserProfileData.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.user = payload;
      state.isLoggedIn = true;
      state.error = null;
    },
    [getUserProfileData.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || error || "error";
      state.user = {};
    },
    // edit user profile
    [editUserProfileData.pending]: (state) => {
      state.loading = true;
    },
    [editUserProfileData.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.user = payload;
      state.isLoggedIn = true;
      state.error = null;
    },
    [editUserProfileData.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || error || "error";
      state.user = {};
    },
    // userCv
    [usersCv.pending]: (state) => {
      state.loading = true;
    },
    [usersCv.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.cv = payload;
      state.error = null;
    },
    [usersCv.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || "error";
      state.cv = null;
    },
  },
});

export const { logInUser } = authSlice.actions;
export default authSlice.reducer;
