import server from "./server";

export const getUserProfileDataApi = async () => {
  const userId = localStorage.getItem("userId");
  const response = await server.get(`user/profile/${userId}`);
  return response?.data?.data;
};

// TODO error cors error from backend should be fixed by shierf
export const editUserProfileDataApi = async ({ data }) => {
  const response = await server.put("user/profile", data);
  return response?.data?.data;
};
