import { createSlice } from "@reduxjs/toolkit";

const metaSlice = createSlice({
  name: "metaData",
  initialState: {
    locale: "ar",
  },
  reducers: {
    changeLocale: (state, { payload }) => {
      return {
        ...state,
        locale: payload,
      };
    },
  },
});

export const { changeLocale } = metaSlice.actions;
export default metaSlice.reducer;
