import server from "./server";

export const getAllTripsApi = async () => {
  const response = await server.get(`trips`);
  return response?.data?.data;
};

export const getHajjTripsApi = async () => {
  const response = await server.get("trips/hajj/1");
  return response?.data?.data;
};
export const getUmraTripsApi = async () => {
  const response = await server.get("trips/umra/1");
  return response?.data?.data;
};

export const getSingleHajjTripApi = async (id) => {
  // console.log(id, "id");
  const response = await server.get(`/trip/hajj/${id}`);
  return response?.data?.data;
};
export const getSingleUmraTripApi = async (id) => {
  const response = await server.get(`/trip/umra/${id}`);
  return response?.data?.data;
};
