import React from "react";
import { Col, Container, Row } from "react-bootstrap";

// needed asssets

// svgs
// import { ReactComponent as LungIcon } from "assets/svgs/lungicon.svg";
// import { ReactComponent as SearchIcon } from "assets/svgs/searchIcon.svg";
import { ReactComponent as LocationIcon } from "assets/svgs/location.svg";
import { ReactComponent as PhoneIcon } from "assets/svgs/phone.svg";

// images
import instagram from "assets/images/Instagram_logo_2016.svg.png";
import linkedin from "assets/images/linkedIn_PNG39.png";
import snapChat from "assets/images/snapchat.png";

import facebook from "assets/images/SeekPng.com_facebook-logo-png_4367.png";
import twitter from "assets/images/twitter-logo.png";

import googlePlay from "assets/images/google-play-app.svg";
import appStore from "assets/images/png-store.svg";
import galleryStore from "assets/images/appGallery.svg";

// needed styles
import styles from "./index.module.scss";
import { NavLink } from "react-router-dom";
import { v4 } from "uuid";
import { useIntl } from "react-intl";

const Footer = () => {
  const intl = useIntl();

  const footerData = {
    first: {
      name: "our-services",
      links: [
        {
          name: " ",
          link: "/touristResorts",
        },
        {
          name: " ",
          link: "/trips",
        },
        {
          name: " ",
          link: "/createCv",
        },
        {
          name: " ",
          link: "/employmentSubscription",
        },
        {
          name: "  ",
          link: "/trips:tripId ",
        },
        {
          name: "  ",
          link: "/searchforemployee",
        },
        {
          name: " ",
          link: "/tourismDepartment",
        },
      ],
    },
    second: {
      name: "my-account",
      links: [
        {
          name: "",
          link: "/followUpOrders",
        },
        {
          name: " ",
          link: "/cart",
        },
        {
          name: " ",
          link: "/contactUs",
        },
        {
          name: " ",
          link: "/login",
        },
        {
          name: " ",
          link: "/signup",
        },
        {
          name: " ",
          link: "/profile",
        },
        {
          name: " ",
          link: "/docmentrequest",
        },
      ],
    },
    third: {
      name: "our-offices",
      options: [
        {
          name: "  -  ",
        },
        {
          name: "  -  ",
        },
      ],
      phone: "+20229478421",
      description:
        "         ,   ,       9   5 ",
    },
    fourth: {
      name: "our-social",
      // map: " ",
      // email: "info@tasshira.com.eg",
      // phone: "+20226709895",
    },
  };

  const socialIcons = [
    {
      src: instagram,
    },
    {
      src: facebook,
    },
    {
      src: twitter,
    },
    {
      src: linkedin,
    },
    {
      src: snapChat,
    },
  ];

  const appStoreData = [
    {
      src: googlePlay,
      link: "/",
    },
    {
      src: appStore,
      link: "/",
    },
    {
      src: galleryStore,
      link: "/",
    },
  ];
  return (
    <footer className={styles[""]}>
      <Container>
        <Row>
          <Col lg={3} md={6} xs={12}>
            <h1 className={styles["nav-header"]}>
              {intl.formatMessage({ id: footerData?.first?.name })}
            </h1>
            <ul>
              {footerData?.first?.links.map((link, index) => (
                <li key={v4()}>
                  <NavLink to={`${link.link}`} className={styles["nav-link"]}>
                    {link?.name}
                  </NavLink>
                </li>
              ))}
            </ul>
          </Col>
          <Col lg={3} md={6} xs={12}>
            <h1 className={styles["nav-header"]}>
              {intl.formatMessage({ id: footerData?.second?.name })}
            </h1>
            <ul>
              {footerData?.second?.links.map((link, index) => (
                <li key={v4()}>
                  <NavLink to={`${link.link}`} className={styles["nav-link"]}>
                    {link?.name}
                  </NavLink>
                </li>
              ))}
            </ul>
          </Col>
          <Col lg={3} md={6} xs={12}>
            <h1 className={styles["nav-header"]}>
              {intl.formatMessage({ id: footerData?.third?.name })}
            </h1>
            <div className={styles["place-info"]}>
              <select name="" id="">
                {footerData?.third?.options.map((option) => (
                  <option value={option?.name} key={v4()}>
                    {option?.name}
                  </option>
                ))}
              </select>
              <div className={styles["place-description"]}>
                <span>
                  <LocationIcon />
                </span>
                <p>{footerData?.third?.description}</p>
              </div>
            </div>
            <div className={styles["phone-info"]}>
              <PhoneIcon />
              <span> </span>
              <span>{footerData?.third?.phone}</span>
            </div>
          </Col>
          <Col lg={3} md={6} xs={12}>
            <h1 className={styles["nav-header"]}>
              {intl.formatMessage({ id: footerData?.fourth?.name })}
            </h1>
            <div className={styles["contact-info"]}>
              <h5>{footerData?.fourth?.map}</h5>
              <h5>{footerData?.fourth?.email}</h5>
              <h5>{footerData?.fourth?.phone}</h5>
            </div>
            <div className={styles["social-icons"]}>
              {socialIcons?.map((icon) => (
                <div className={styles["social-icon"]} key={v4()}>
                  <a href="http://" target="_blank" rel="noopener noreferrer">
                    <img src={icon?.src} alt="social-icon" />
                  </a>
                </div>
              ))}
            </div>
            <div className={styles["app-store"]}>
              {appStoreData?.map((icon) => (
                <div className={styles["social-icon"]} key={v4()}>
                  <a
                    href={icon?.link}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <img
                      src={icon?.src}
                      alt="social-icon"
                      width={100}
                      height={50}
                    />
                  </a>
                </div>
              ))}
            </div>
          </Col>
        </Row>
        <Row>
          <Col xs={12} className={styles["wrapper"]}>
            <p className={styles["copy-rights"]}>
              .  2003-2022 Taashira.com, Inc  .  Taashira
                  
            </p>
          </Col>
        </Row>
      </Container>
    </footer>
  );
};

export default Footer;
