import React from "react";
import { Container } from "react-bootstrap";

// needed asssets
// import appLogo from "assets/images/Logo.PNG";
// import userIcon from "assets/images/userIcon.png";
// import { ReactComponent as LungIcon } from "assets/svgs/lungicon.svg";
// import { ReactComponent as SearchIcon } from "assets/svgs/searchIcon.svg";

// needed styles
import styles from "./index.module.scss";
import { NavLink } from "react-router-dom";
// import { useIntl } from "react-intl";
import { toggleSideNavBar } from "helpers/functions";
import { v4 } from "uuid";
import { useIntl } from "react-intl";

const SideNavBar = () => {
  const intl = useIntl();

  const handleCloseSideNav = () => {
    toggleSideNavBar();
  };

  const footerData = [
    {
      name: " ",
      link: "/touristResorts",
    },
    {
      name: " ",
      link: "/trips",
    },
    {
      name: " ",
      link: "/createCv",
    },
    {
      name: " ",
      link: "/employmentSubscription",
    },
    {
      name: "  ",
      link: "/trips:tripId ",
    },
    {
      name: "  ",
      link: "/searchforemployee",
    },
    {
      name: " ",
      link: "/tourismDepartment",
    },
  ];

  return (
    <nav>
      <Container>
        <div id="mySidenav" className={styles["sidenav"]}>
          <div>
            <span className={styles["closebtn"]} onClick={handleCloseSideNav}>
              &times;
            </span>
            <span className={styles["nav-header"]}>
              {intl.formatMessage({ id: "list" })}
            </span>
          </div>
          <ul>
            {footerData?.map((link, index) => (
              <li key={v4()}>
                <NavLink
                  onClick={handleCloseSideNav}
                  to={`${link.link}`}
                  className={styles["nav-link"]}
                >
                  {link?.name}
                </NavLink>
              </li>
            ))}
          </ul>
        </div>
      </Container>
    </nav>
  );
};

export default SideNavBar;
