import React, { Suspense, useEffect, useState } from "react";
import runtimeEnv from "@mars/heroku-js-runtime-env";
import { Routes, Route } from "react-router-dom";
import { v4 } from "uuid";
import { IntlProvider } from "react-intl";
import ar from "locale/ar.json";
import en from "locale/en.json";

// app routes
import appRoutes from "constant/routes";

// swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "swiper/css/grid";
import "swiper/css/thumbs";
import "swiper/css/free-mode";
import "swiper/css/controller";
import "react-toastify/dist/ReactToastify.css";
// needed styels
import "bootstrap/dist/css/bootstrap.min.css";
import "styles/globales.scss";
// import required modules
import SwiperCore, { Pagination, Navigation, Grid, Autoplay } from "swiper";

// needed components
import LayoutComponent from "components/shared/Layout/LayoutComponent";
import ViewLoader from "components/shared/ViewLoader/ViewLoader";
import { onAuthStateChanged } from "firebase/auth";
import { authentication } from "firebaseConfig/firebaseConfig";
import { useDispatch, useSelector } from "react-redux";
import { getUserProfileData, logInUser, usersCv } from "store/auth/slice";
import { getHajjTrips, getUmraTrips } from "store/trips/slice";
import { changeLocale } from "store/meta/slice";

import {
  getNationality,
  getSpecialties,
  getSubscriptionTypes,
} from "store/empolyee/slice";
import { getDocumentsTypes } from "store/document/slice";
import { ToastContainer } from "react-toastify";
import AuthRoute from "components/shared/authentication/authRoute";

// initial data
SwiperCore.use([Pagination, Navigation, Grid, Autoplay]);
const messages = { ar, en };

function App() {
  const state = useSelector((state) => state);
  const dispatch = useDispatch();
  const env = runtimeEnv();

  useEffect(() => {
    dispatch(getUmraTrips());
    dispatch(getHajjTrips());
    if (state.authentication.isLoggedIn) {
      dispatch(getUserProfileData());
    }

    // dispatch(getAllTrips());
  }, [dispatch]);

  const defaultlocale = localStorage["locale"] ? localStorage["locale"] : "ar";
  // planing to make it a globale state in redux store or pass it as a prop to layout if the use will only be able to change it form layout only
  const [currentLocale, setCurrentLocale] = useState(defaultlocale);

  useEffect(() => {
    const lang = localStorage["locale"] ? localStorage["locale"] : "ar";
    const dir = lang === "ar" ? "rtl" : "ltr";
    document.documentElement.dir = dir;
    document.body.style.direction = dir;
    document.body.setAttribute("dir", dir);
    dispatch(changeLocale(currentLocale));
  }, [currentLocale]);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(authentication, (authUser) => {
      if (authUser) {
        dispatch(logInUser(authUser));
        dispatch(getUserProfileData());
        dispatch(usersCv());
      }
      authUser
        ? localStorage.setItem("authUser", JSON.stringify(authUser))
        : localStorage.removeItem("authUser");
    });
    return unsubscribe;
  }, [dispatch]);

  useEffect(() => {
    dispatch(getSpecialties());
    dispatch(getSubscriptionTypes());
    dispatch(getNationality());
    dispatch(getDocumentsTypes());
  }, []);

  return (
    <IntlProvider
      messages={messages?.[currentLocale]}
      defaultLocale={"ar"}
      locale={currentLocale}
    >
      <LayoutComponent {...{ currentLocale, setCurrentLocale }}>
        <Routes>
          {appRoutes.map(({ path, Element, auth }) => (
            <Route
              key={v4()}
              path={path}
              element={
                <Suspense fallback={<ViewLoader />}>
                  {Object.keys(state)
                    .map((key) => state?.[key].loading)
                    ?.some((item) => item === true) ? (
                    <ViewLoader />
                  ) : (
                    <>
                      {auth ? (
                        <AuthRoute>
                          <Element />
                        </AuthRoute>
                      ) : (
                        <Element />
                      )}
                    </>
                  )}
                </Suspense>
              }
            />
          ))}
          {/* FIXME check if all initial proccess have been loaded */}
          <Route path="/loader" element={<ViewLoader />} />
        </Routes>
      </LayoutComponent>
      <ToastContainer
        position="bottom-left"
        autoClose={5000}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
      />
    </IntlProvider>
  );
}

export default App;
