import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  getAllTripsApi,
  getHajjTripsApi,
  getSingleHajjTripApi,
  getSingleUmraTripApi,
  getUmraTripsApi,
} from "api/trips";

export const getAllTrips = createAsyncThunk(
  "trips/getAllTrips",
  getAllTripsApi
);
export const getHajjTrips = createAsyncThunk(
  "trips/getHajjTrips",
  getHajjTripsApi
);

export const getUmraTrips = createAsyncThunk(
  "trips/getUmraTrips",
  getUmraTripsApi
);

export const getSingleHajjTrip = createAsyncThunk(
  "trips/getSingleHajjTrip",
  getSingleHajjTripApi
);

export const getSingleUmraTrip = createAsyncThunk(
  "trips/getSingleUmraTrip",
  getSingleUmraTripApi
);

const tripsReducer = createSlice({
  name: "trips",
  initialState: {
    allTrips: [],
    hajjTrips: [],
    umraTrips: [],
    singleTrip: {},
    loading: false,
    error: null,
  },
  reducers: {},
  extraReducers: {
    // get all trips
    [getAllTrips.pending]: (state) => {
      state.loading = true;
    },
    [getAllTrips.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.allTrips = payload;
      state.error = null;
    },
    [getAllTrips.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || error || "error";
      state.user = {};
    },

    // get umra trips
    [getUmraTrips.pending]: (state) => {
      state.loading = true;
    },
    [getUmraTrips.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.umraTrips = payload;
      state.error = null;
    },
    [getUmraTrips.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || error || "error";
      state.user = {};
    },

    // get hajj trips
    [getHajjTrips.pending]: (state) => {
      state.loading = true;
    },
    [getHajjTrips.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.hajjTrips = payload;
      state.error = null;
    },
    [getHajjTrips.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || error || "error";
      state.user = {};
    },

    // get single hajj trip
    [getSingleUmraTrip.pending]: (state) => {
      state.loading = true;
    },
    [getSingleUmraTrip.fulfilled]: (state, { payload }) => {
      console.log(payload, "payload");

      state.loading = false;
      state.singleTrip = payload;
      state.error = null;
    },
    [getSingleUmraTrip.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || error || "error";
      state.user = {};
    },

    // get single umra trip
    [getSingleUmraTrip.pending]: (state) => {
      state.loading = true;
    },
    [getSingleUmraTrip.fulfilled]: (state, { payload }) => {
      console.log(payload, "payload");
      state.loading = false;
      state.singleTrip = payload;
      state.error = null;
    },
    [getSingleUmraTrip.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || error || "error";
      state.user = {};
    },
  },
});

export default tripsReducer.reducer;
