export const toggleSideNavBar = () => {
  document?.getElementById("mySidenav")?.classList?.toggle("fire");
};
export const convertDateValue = (date) => {
  // 12-12-12
  return new Date(date).toISOString().slice(0, 10);
};

export const scrollToErrorField = (element, errorClass) => {
  element.scrollIntoView({ behavior: "smooth" });
};
