import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { getDocumentTypes } from "api/documents";

export const getDocumentsTypes = createAsyncThunk(
  "documents/getDocumentTypes",
  getDocumentTypes
);

const documents = createSlice({
  name: "documents",
  initialState: {
    documentTypes: [],
    loading: false,
    error: null,
  },
  reducers: {
    addDocumentTypes: (state, { payload }) => {
      return {
        ...state,
        loading: false,
        error: null,
        documentTypes: payload,
      };
    },
  },
  extraReducers: {
    // subscriptionTypes
    [getDocumentsTypes.pending]: (state) => {
      state.loading = true;
    },
    [getDocumentsTypes.fulfilled]: (state, { payload }) => {
      state.loading = false;
      state.documentTypes = payload;
      state.error = null;
    },
    [getDocumentsTypes.rejected]: (state, { error }) => {
      state.loading = false;
      state.error = error?.message || "error";
      state.documentTypes = [];
    },
  },
});

export const { addDocumentTypes } = documents.actions;
export default documents.reducer;
